  --primary: #69C798;
  --clay-outset: 8px 8px 16px 0 rgba(0, 0, 0, 0.152);
  --clay-inset-secondary: 8px 8px 16px 0 rgba(255, 255, 255, 0.131);
}



.App {
  text-align: center;
  margin: 0 auto;
  width: 100%;
  user-select: none;
}
.floor
{
  height: 12vw;
  width: auto;
